import React, { useEffect, useState } from "react";
import { Helmet } from 'react-helmet';
import "../assets/css/style.css";
import anime from "animejs/lib/anime.es.js";



const contentSlides = [
  {
    headline: "Приветствуем вас!",
    text: "Добро пожаловать на нашу платформу! Здесь вы найдете множество возможностей для развития и обучения. Присоединяйтесь к нам!",
    buttonText: "В редактор",
    href: "./demo",
  },
  {
    headline: "Больше информации",
    text: "Мы предлагаем разнообразные решения, которые помогут вам достичь ваших целей. Ознакомьтесь с нашим предложением и выберите подходящее.",
    buttonText: "Узнать больше",
    href: "./about",
  },
  {
    headline: "Современные технологии",
    text: "Наши технологии направлены на улучшение вашего опыта. Мы внедряем новейшие решения, которые помогут вам в работе.",
    buttonText: "Подробнее",
    href: "./docs/uses",
  },
  {
    headline: "Напишите нам",
    text: "Если у вас есть вопросы или предложения, не стесняйтесь обращаться к нам. Мы всегда готовы помочь вам и ответить на ваши запросы.",
    buttonText: "Связаться",
    href: "./docs/newsletter",
  },
];

const WebBuilder = () => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const handleNavClick = (index) => {
    if (index !== currentIndex) {
      animateLinkChange(index);
      animateTransition(index);
    }
  };

  const animateLinkChange = (newIndex) => {
    const direction = newIndex > currentIndex ? 'next' : 'prev';
    handleGalleryClick(direction)
    const prevLink = document.querySelector(`.nav a.active`);
    if (prevLink) {
      anime({
        targets: prevLink,
        scale: [1.2, 1],
        color: "#ffffff",
        duration: 100,
        easing: "easeOutExpo",
      });
      prevLink.classList.remove("active");
    }
    const newLink = document.querySelector(`.nav a:nth-child(${newIndex + 1})`);
    if (newLink) {
      anime({
        targets: newLink,
        scale: [1, 1.2],
        color: "#4986FF",
        duration: 100,
        easing: "easeOutExpo",
      });
      newLink.classList.add("active");
    }
  };

  const handleGalleryClick = (direction) => {
    let newIndex = currentIndex;
    if (direction === 'prev') {
      newIndex = (currentIndex - 1 + contentSlides.length) % contentSlides.length;
      animateCounter('up'); // Animate counter up
    } else if (direction === 'next') {
      newIndex = (currentIndex + 1) % contentSlides.length;
      animateCounter('down'); // Animate counter down
    }
    const prevLink = document.querySelector(`.nav a.active`);
    const newLink = document.querySelector(`.nav a:nth-child(${newIndex + 1})`);
    if (prevLink) {
      anime({
        targets: prevLink,
        scale: [1.2, 1],
        color: "#ffffff",
        duration: 100,
        easing: "easeOutExpo",
      });
      prevLink.classList.remove("active");
    }
    if (newLink) {
      anime({
        targets: newLink,
        scale: [1, 1.2],
        color: "#4986FF",
        duration: 100,
        easing: "easeOutExpo",
      });
      newLink.classList.add("active");
    }
    animateTransition(newIndex);
  };

  const animateCounter = (direction) => {
    const counterText = document.querySelector(".counter-text");
    const translate = direction === 'up' ? -120 : 120;
    anime({
      targets: counterText,
      translateY: [0, translate, -translate, 0],
      opacity: [1, 0, 0, 1],
      duration: 1000,
      easing: "easeInOutSine",
    })
  };
  
  const updateLoader = (index) => {
    const counterText = document.querySelector("#gallery-loader h2");
    const loaderLine = document.querySelector(".loader-line");
    counterText.innerText = String(index + 1).padStart(2, '0');
    const widthPercentage = ((index + 1) / contentSlides.length) * 100;
    anime({
      targets: loaderLine,
      width: `${widthPercentage}%`,
      duration: 500,
      easing: "easeOutExpo",
    });
  };

  const animateTransition = (newIndex) => {
    anime({
      targets: ".text-section",
      translateX: [0, 600],
      opacity: [1, 0],
      duration: 500,
      easing: "easeInExpo",
      complete: () => {
        setCurrentIndex(newIndex);
        anime({
          targets: ".text-section",
          translateX: [-600, 0],
          opacity: [0, 1],
          duration: 500,
          easing: "easeOutExpo",
        });
        updateLoader(newIndex);
      },
    });
  };

  useEffect(() => {
    const firstLink = document.querySelector(".nav a");
    const timeline = anime.timeline({
      easing: "easeOutExpo",
      complete: () => {
        document.body.style.pointerEvents = "auto";
        document.querySelector("html").style.cssText = ""
      }
    });
    document.querySelector("html").style.cssText = "overflow: hidden"
    document.body.style.pointerEvents = "none";
    timeline
      .add({
        targets: ".nav-wrapper",
        width: ["0px", "1200px"],
      })
      .add({
        targets: ".logo",
        width: ["0px", "230px"],
      })
      .add({
        targets: ".logo h1, .nav a",
        opacity: [0, 1],
        delay: (el, i) => 100 * i,
      }, "-=700")
      .add({
        targets: ".text-section",
        translateY: [100, 0],
        opacity: [0, 1],
      }, "-=700")
      .add({
        targets: ".image-section",
        translateY: [-100, 0],
        opacity: [0, 1],
      }, "-=1000")
      .add({
        targets: "#gallery-headline",
        height: ["0px", "80px"],
        opacity: [0, 1],
      }, "-=1000")
      .add({
        targets: "#gallery-loader",
        height: ["0px", "135px"],
        opacity: [0, 1],
      }, "-=700")
      .add({
        targets: ".sidebar-section",
        height: ["0px", "1100px"],
      })
      .add({
        targets: ".ham-line",
        translateX: [500, 0],
        opacity: [0, 1],
        duration: 800,
        delay: (el, i) => 100 * i,
      }, "-=700")
      .add({
        targets: ".social a",
        translateX: [500, 0],
        opacity: [0, 1],
        delay: (el, i) => 200 * i,
      }, "-=1000")
      .add({
        targets: ".gallery-nav div",
        translateY: [500, 0],
        opacity: [0, 1],
        delay: (el, i) => 200 * i,
      }, "-=1000")
      .add({
        targets: firstLink,
        scale: [1, 1.2],
        color: "#4986FF",
        duration: 100,
        easing: "easeOutExpo",
      });

    const navLinks = document.querySelectorAll(".nav a");
    navLinks.forEach((navLink) => {
      navLink.addEventListener("mouseenter", () => {
        if (!navLink.classList.contains("active")) {
          const letters = navLink.querySelectorAll(".letter");
          anime.remove(letters);
          anime.timeline({
            targets: letters,
          })
          .add({
            translateY: (el, i) => [0, -30 * Math.pow(-1, i)],
            opacity: [1, 0],
            easing: "easeInExpo",
            duration: 500,
            delay: (el, i) => 30 * i,
          })
          .add({
            translateY: (el, i) => [40 * Math.pow(-1, i), 0],
            opacity: [0, 1],
            easing: "easeOutExpo",
            duration: 500,
            delay: (el, i) => 30 * i,
          });
        }
      });
    });

    const socialLinks = document.querySelectorAll(".social a");
    socialLinks.forEach((socialLink) => {
      socialLink.addEventListener("mouseenter", () => {
        const letters = socialLink.querySelectorAll(".letter");
        const letterArray = Array.from(letters);
        const midIndex = Math.ceil(letterArray.length / 2);
        const [halfArray1, halfArray2] = [letterArray.slice(0, midIndex), letterArray.slice(midIndex)];

        anime.remove(halfArray1);
        anime.timeline({
          targets: halfArray1,
        })
        .add({
          translateX: (el, i) => [0, -30],
          opacity: [1, 0],
          easing: "easeInExpo",
          duration: 500,
          delay: (el, i) => 50 * i,
        })
        .add({
          translateX: (el, i) => [30, 0],
          opacity: [0, 1],
          easing: "easeOutExpo",
          duration: 500,
          delay: (el, i) => 50 * i,
        });
        anime.remove(halfArray2);
        anime.timeline({
          targets: halfArray2,
        })
        .add({
          translateX: (el, i) => [0, 30],
          opacity: [1, 0],
          easing: "easeInExpo",
          duration: 500,
          delay: (el, i) => 50 * i,
        })
        .add({
          translateX: (el, i) => [-30, 0],
          opacity: [0, 1],
          easing: "easeOutExpo",
          duration: 500,
          delay: (el, i) => 50 * i,
        });
      });
    });

    const galleryLinks = document.querySelectorAll(".gallery-nav div");
    galleryLinks.forEach((galleryLink) => {
      galleryLink.addEventListener("mouseenter", () => {
        anime.remove(galleryLink);
        anime({
          targets: galleryLink,
          scale: 1.1,
          translateX: -5,
          translateY: -5,
          easing: "easeOutExpo",
        });
      });
      galleryLink.addEventListener("mouseleave", () => {
        anime.remove(galleryLink);
        anime({
          targets: galleryLink,
          scale: 1,
          translateX: 0,
          translateY: 0,
          easing: "easeOutExpo",
        });
      });
    });

    const logo = document.querySelector(".logo");
    logo.addEventListener("mouseenter", () => {
      anime({
        targets: logo,
        scale: 1.1,
        translateX: 5,
        translateY: 5,
        duration: 500,
        backgroundColor: "#fff",
        color: "#0050EF",
        easing: "easeOutExpo",
      });
    });
    logo.addEventListener("mouseleave", () => {
      anime({
        targets: logo,
        scale: 1,
        translateX: 0,
        translateY: 0,
        duration: 500,
        backgroundColor: "#0050EF",
        color: "#fff",
        easing: "easeOutExpo",
      });
    });

    const hamburger = document.querySelector(".hamburger");
    const closeButton = document.querySelector(".hamburger-close");    
    const topLine = document.getElementById("top");
    const middleLine = document.getElementById("middle");
    const bottomLine = document.getElementById("bottom");
    
    const resetHamburger = () => {
      anime.remove([topLine, middleLine, bottomLine]);
      topLine.style.transform = "";
      middleLine.style.transform = "";
      bottomLine.style.transform = "";
      topLine.style.height = "3px";
      middleLine.style.height = "3px";
      bottomLine.style.height = "3px";
      topLine.style.backgroundColor = "#fff";
      middleLine.style.backgroundColor = "#fff";
      bottomLine.style.backgroundColor = "#fff";
    };
    
    hamburger.addEventListener("mouseenter", () => {
      const menu = document.querySelector(".hamburger-menu");
      if (!menu.classList.contains("active")) {
        anime.remove([topLine, middleLine, bottomLine]);
        anime.timeline({
          targets: [topLine, bottomLine, middleLine],
          duration: 300,
          easing: "easeInOutSine",
        })
        .add({
          translateY: (el) => {
            if (el === topLine) return 23;
            if (el === bottomLine) return -25;
            return 0;
          },
          height: (el) => {
            if (el === topLine) return 5;
            if (el === bottomLine) return 10;
            return 0;
          },
          backgroundColor: (el) => {
            if (el === topLine) return "#C68A4E";
            if (el === bottomLine) return "#D19A6A";
            if (el === middleLine) return "#6F4C3E";
          },
        })
        .add({
          targets: middleLine,
          height: 15,
          duration: 300,
          easing: "easeInOutSine",
        });
      }
    });
    
    hamburger.addEventListener("mouseleave", () => {
      if (!document.querySelector(".hamburger-menu").classList.contains("active")) {
        anime.remove([topLine, middleLine, bottomLine]);
        anime.timeline({
          targets: [topLine, bottomLine, middleLine],
          duration: 300,
          easing: "easeInOutSine",
        })
        .add({
          translateY: 0,
          height: (el) => (el === topLine || el === bottomLine ? 3 : 0),
          backgroundColor: "#fff",
        })
        .add({
          targets: middleLine,
          height: 3,
          duration: 300,
          easing: "easeInOutSine",
        });
      }
    });

    hamburger.addEventListener("click", () => {
      const first = document.getElementById("first");
      const second = document.getElementById("second")
      const third = document.getElementById("third");
      const menu = document.querySelector(".hamburger-menu");
      const isActive = menu.classList.toggle("active");
      resetHamburger();
      anime({
        targets: menu,
        translateY: isActive ? ["-100%", 0] : [0, "-100%"],
        duration: 700,
        easing: "easeOutBounce",
        begin: () => {
          menu.style.display = "flex"
        },
        complete: () => {
          if (isActive) {
              anime.timeline()
              .add({
                targets: [first, second, third, closeButton],
                margin: (el) => {
                  if (el === second) return "-3px 0px";
                  return 0
                },
                rotate: (el) => {
                  if (el === first) return 45;
                  if (el === third) return -45;
                  return 0
                },
                opacity: (el) => {
                  if (el === second) return 0;
                  return 1
                },
                translateY: 0,
                duration: 400,
                easing: "easeInOutSine",
              })
              .add({
                targets: closeButton,
                paddingTop: [0, 10, 17.5],
                opacity: [0, 0.75, 1],
                scale: [0, 0.75, 1, 1.1, 1],
                rotate: [0, 30, -15, 10, 0],
                duration: 1500,
                easing: "easeOutBounce",
                begin: function() {
                  closeButton.style.display = "flex";
                },
              }, "-=900")
          }        
        }
      });

    });
    
    closeButton.addEventListener("click", () => {
      const menu = document.querySelector(".hamburger-menu");
      const isActive = menu.classList.toggle("active");
      resetHamburger();
      anime({
        targets: menu,
        translateY: isActive ? ["-100%", 0] : [0, "-100%"],
        duration: 700,
        easing: "easeOutBounce",
        complete: () => {
          menu.style.display = "none"
        }
      });
      if (!isActive) {
        anime({
          targets: closeButton,
          translateY: [0, -500],
          easing: "easeInOutSine",
          complete: function() {
            closeButton.style.display = "none";
            closeButton.style.transform = ""
            anime({
              targets: [first, second, third, closeButton],
              margin: (el) => {
                if (el === second) return "5px 0px";
                return 0
              },
              rotate: 0,
              opacity: 1,
              translateY: 0,
            })
          },
        })
        }   
    });

    return () => {
      navLinks.forEach(navLink => {
        navLink.removeEventListener("mouseenter", () => {});
      });
      socialLinks.forEach(socialLink => {
        socialLink.removeEventListener("mouseenter", () => {});
      });
      galleryLinks.forEach(galleryLink => {
        galleryLink.removeEventListener("mouseenter", () => {});
        galleryLink.removeEventListener("mouseleave", () => {});
      });
      logo.removeEventListener("mouseenter", () => {});
      logo.removeEventListener("mouseleave", () => {});
      hamburger.removeEventListener("mouseenter", () => {});
      hamburger.removeEventListener("mouseleave", () => {});
    };

  }, []);

  return (
    <div className="main">
      <Helmet>
        <title>Навигация</title>
      </Helmet>
      <div className="hamburger-close">
        <div className="close-line" id="first"></div>
        <div className="close-line" id="second"></div>
        <div className="close-line" id="third"></div>
      </div>
      <div className="hamburger-menu">
        <div className="hamburger-content">
          <h3>Основное</h3>
          <div className="hamburger-box">
            <div className="hamburger-item">
              <nav>
                <ul>
                  <li><a href="/demo">Редактор</a></li>
                  <li><a href="/main">Главная</a></li>
                  <li><a href="/about">Информация</a></li>
                </ul>
              </nav>
            </div>
          </div>
        </div>
        <div className="hamburger-content">
          <h3>Документация</h3>
          <div className="hamburger-box">
            <div className="hamburger-item">
              <a href="/docs/getting-started">Начать</a>
              <ul>
                <li><a href="/docs/blocks">Библиотека компонентов</a></li>
                <li><a href="/docs/controls">Управление</a></li>
                <li><a href="/docs/guides">Гайды</a></li>
              </ul>
            </div>
            <div className="hamburger-item">
              <a href="/docs/advanced">Для продвинутых</a>
              <ul>
                <li><a href="/docs/about">О проекте</a></li>
                <li><a href="/docs/advanced/how-it-works">Как устроено</a></li>
                <li><a href="/docs/guides/write-a-custom-block">Кастомные компоненты</a></li>
              </ul>
            </div>
            <div className="hamburger-item">
              <a href="/docs/uses">Технологии</a>
              <ul>
                <li><a href="/docs/advanced/transforms">Transforms</a></li>
                <li><a href="/docs/advanced/queries">Queries</a></li>
                <li><a href="/docs/advanced/blocks-docs-generation">Generation</a></li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div className="nav-wrapper">
        <a className="logo" href="./">
          <p>WebBuilder</p>
        </a>
        <div className="nav">
          {["Главная", "О проекте", "Технологии", "Контакты"].map((link, index) => (
            <a
              href="#"
              key={index}
              className={`inactive ${currentIndex === index ? "active" : ""}`}
              onClick={(e) => {
                e.preventDefault();
                handleNavClick(index);
              }}
            >
              {link.split("").map((letter, letterIndex) => (
                <div 
                  className="letter" 
                  key={letterIndex} 
                  style={index === 1 && letterIndex === 0 ? { marginRight: "5px" } : {}}>
                  {letter}
                </div>
              ))}
            </a>
          ))}
        </div>
        
      </div>
      <div className="column-wrapper">
        <section className="text-section">
          <h1 id="col-baby">{contentSlides[currentIndex].headline}</h1>
          <p>{contentSlides[currentIndex].text}</p>
          <div className="details-btn">
            <a href={contentSlides[currentIndex].href}>{contentSlides[currentIndex].buttonText}</a>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path fillRule="evenodd" clipRule="evenodd" d="M12 4C12.5523 4 13 4.44772 13 5V19C13 19.5523 12.5523 20 12 20C11.4477 20 11 19.5523 11 19V5C11 4.44772 11.4477 4 12 4Z" fill="#ffffff"/>
              <path fillRule="evenodd" clipRule="evenodd" d="M4.29289 11.2929C4.68342 10.9024 5.31658 10.9024 5.70711 11.2929L12 17.5858L18.2929 11.2929C18.6834 10.9024 19.3166 10.9024 19.7071 11.2929C20.0976 11.6834 20.0976 12.3166 19.7071 12.7071L12.7071 19.7071C12.3166 20.0976 11.6834 20.0976 11.2929 19.7071L4.29289 12.7071C3.90237 12.3166 3.90237 11.6834 4.29289 11.2929Z" fill="#ffffff"/>
            </svg>
          </div>
        </section>
        <section className="image-section">
          <div className="gallery-counter">
            <div id="gallery-headline"></div>
            <div id="gallery-loader">
            <h2 className="counter-text">01</h2>
              <div className="loader">
                <div className="loader-line" style={{ width: '25%' }}></div>
              </div>
            </div>
          </div>
        </section>
        <section className="sidebar-section">
          <div className="hamburger">
            <div className="ham-line" id="top"></div>
            <div className="ham-line" id="middle"></div>
            <div className="ham-line" id="bottom"></div>
          </div>
          <div className="social">
            <a href="https://github.com">
              {["G", "i", "t", "H", "u", "b"].map((letter, index) => (
                <div className="letter" key={index}>{letter}</div>
              ))}
            </a>
            <a href="https://t.me">
              {["T", "e", "l", "e", "g", "r", "a", "m"].map((letter, index) => (
                <div className="letter" key={index}>{letter}</div>
              ))}
            </a>
          </div>
        </section>
      </div>
      <div className="gallery-nav">
          <div onClick={() => handleGalleryClick('prev')} id="blue-bg" className="gallery-nav-item">
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path fillRule="evenodd" clipRule="evenodd" d="M15.7071 5.29289C16.0976 5.68342 16.0976 6.31658 15.7071 6.70711L10.4142 12L15.7071 17.2929C16.0976 17.6834 16.0976 18.3166 15.7071 18.7071C15.3166 19.0976 14.6834 19.0976 14.2929 18.7071L8.29289 12.7071C7.90237 12.3166 7.90237 11.6834 8.29289 11.2929L14.2929 5.29289C14.6834 4.90237 15.3166 4.90237 15.7071 5.29289Z" fill="#ffffff" />
            </svg>
          </div>
          <div onClick={() => handleGalleryClick('next')} id="white-bg" className="gallery-nav-item">
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path fillRule="evenodd" clipRule="evenodd" d="M15.7071 5.29289C16.0976 5.68342 16.0976 6.31658 15.7071 6.70711L10.4142 12L15.7071 17.2929C16.0976 17.6834 16.0976 18.3166 15.7071 18.7071C15.3166 19.0976 14.6834 19.0976 14.2929 18.7071L8.29289 12.7071C7.90237 12.3166 7.90237 11.6834 8.29289 11.2929L14.2929 5.29289C14.6834 4.90237 15.3166 4.90237 15.7071 5.29289Z" fill="#ffffff" />
            </svg>
          </div>
        </div>
    </div>
    
  );
};

export default WebBuilder;